body {
  height: 100vh;
  filter: drop-shadow(0 0 10px #fff);
  background: radial-gradient(at bottom, #1b2735 0%, #090a0f 100%);
  overflow: hidden;
}

.snow {
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
}

